export class CellBuilderDrvFlag extends CellBuilderFlagIcon {

	iconWidth = ".8rem";

	iconFilter = "var(--filter)";

	protected _getIcon(row: IGridDataRow): string {
		const st = super._getValue(row);
		return st ? "--drv-" + st + "-url" : "";
	}

	protected _getValue(row: IGridDataRow): any {
		const st = super._getValue(row);
		return st ? WSP_DRV.getDrvStateLabel(st as EDrvState) : "";
	}
}

