export class MarkDrvDone extends SrcAction<IShortDescCtx> {
	static SINGLETON = new MarkDrvDone();

	constructor() {
		super('markDrvDone');
		this._label = "Marquer la dÃ©rivation achevÃ©e";
		this._group = "edit";
		this._icon = "/@skin@/wsp/actions/drvDone.svg";
		this.atLeastOne = true;
		this.isNotRemoved = false;
		this._enableSrcPerms = ["action.drv#markDone"];
	}

	isEnabled(ctx: IShortDescCtx): boolean {
		if (ctx.shortDescs.some((sd) => sd.drvState.endsWith("Done"))) return false;
		return super.isEnabled(ctx);
	}

	async execute(ctx: IShortDescCtx, ev?: Event) {
		const wsp = ctx.reg.env.wsp;
		if (await this.confirm(ctx, ev)) {
			await WSP_DRV.srcMarkDrvDone(ctx.reg.env.wsp, ctx.emitter, ctx.shortDescs);
		}
	}

	confirm(ctx: IShortDescCtx, ev?: Event): Promise<boolean> {
		return POPUP.confirm("Confirmez le marquage \"achevÃ©\" de la dÃ©rivation.", ctx.emitter, {okLbl: "Marquer \"achevÃ©\""});
	}
}

