/**
 *
 */
export class RibbonDrv extends RibbonBase {

	protected _initialize(init: any): void {
		super._initialize(init);
		this.reg.env.longDescChange.add(this.refresh.bind(this));
		this.setTooltip(this._tooltip, true);
	}

	template(rib: RibbonDrv): TemplateResult {
		return xhtml`<div class="smallTi">Calque de dÃ©rivation</div><div id="st" data-drv-state="${rib.reg.env.longDesc.drvState}"><img id="ico"/><div class="layer">${WSP_DRV.getDrvStateLayer(rib.reg.env.longDesc.drvState)}</div><div class="done">${WSP_DRV.getDrvStateDone(rib.reg.env.longDesc.drvState)}</div></div>`;
	}

	_tooltip(this: void, rib: RibbonDrv, tooltip: PopupTooltip): IView | null {
		return <div id="ttRoot">
			<BarActions class="vertical" Ã®={{
				reg: rib.reg,
				actions: [rib.reg.getSvc('drv/markDrvDoneAction'), rib.reg.getSvc('drv/deleteDrvOvrAction')],
				actionContext: rib.shortDescCtx,
				uiContext: "dialog",
				disableFullOverlay: true
			} as OBarActionsInit<IShortDescCtx>}/>
		</div>;
	}

	protected _refresh() {
		if (!this.hidden) renderAppend(this.template(this), this.shadowRoot);
	}
}

