	set overridenNew(state: boolean) {this._overridenNewBtn.toggleOn = state}

	get overridenDone(): boolean {return this._overridenDoneBtn.toggleOn}

	set overridenDone(state: boolean) {this._overridenDoneBtn.toggleOn = state}

	get overridenDirty(): boolean {return this._overridenDirtyBtn.toggleOn}

	set overridenDirty(state: boolean) {this._overridenDirtyBtn.toggleOn = state}

	get createdNew(): boolean {return this._createdNewBtn.toggleOn}

	set createdNew(state: boolean) {this._createdNewBtn.toggleOn = state}

	get createdDone(): boolean {return this._createdDoneBtn.toggleOn}

	set createdDone(state: boolean) {this._createdDoneBtn.toggleOn = state}

