		gridContainer.appendChild(<Button onclick={this.toggleColumn} icon="/@skin@/wsp/objects/drvState/dirty.svg" data-column="dirty" title="Items Ã  (re)contrÃ´ler" ui-context="bar"/>);

		// Ligne 'notOverriden'
		gridContainer.appendChild(<Button onclick={this.toggleRow} label="Non surchargÃ©s" data-row="notOverriden" title="Items originaux non surchargÃ©s dans cette dÃ©rivation" ui-context="bar"/>);
		this._notOverridenBtn = <ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drvState/notOverriden.svg" class="notOverriden" title="Items non surchargÃ©s, jamais marquÃ©s 'achevÃ©s'"/> as ButtonToggle;
		this._notOverridenDoneBtn = <ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drvState/notOverridenDone.svg" class="notOverridenDone" title="Items non surchargÃ©s, marquÃ©s 'achevÃ©s'"/> as ButtonToggle;
