		this._overridenDoneBtn = <ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drvState/overridenDone.svg" class="overridenDone" title="Items surchargÃ©s, marquÃ©s 'achevÃ©s'"/> as ButtonToggle;
		this._overridenDirtyBtn = <ButtonToggle onclick={this.toggleButton} icon="/@skin@/wsp/objects/drvState/overridenDirty.svg" class="overridenDirty" title="Items surchargÃ©s, Ã  contrÃ´ler" aria-pressed="true"/> as ButtonToggle;
		gridContainer.append(<div class="bg">{this._overridenNewBtn}</div>, <div class="bg">{this._overridenDoneBtn}</div>, <div class="bg">{this._overridenDirtyBtn}</div>);

